@font-face {
    font-family: 'RobotoCondensed';
    src: url('fonts/RobotoCondensed-Regular.ttf');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'RobotoCondensed';
    src: url('fonts/RobotoCondensed-Bold.ttf');
    font-weight: 700;
    font-style: normal;
}
@font-face {
    font-family: 'RobotoCondensed';
    src: url('fonts/RobotoCondensed-Light.ttf');
    font-weight: 300;
    font-style: normal;
}
html{
	font-family:Arial, sans-serif ;
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: auto;
    background: #04151e;
}
img{
	max-width: 100%;
	height: auto;
}

input:focus, textarea:focus{
	outline: none;
	transition: .2s ease-in;
	border-color: #00ceb8;
}

.container{
	box-sizing: border-box;
}
.swiper-container{
	width: 100%;
	height: 100%;
    position: absolute;
}
.banner {
	background: url(../img/sec-1-bg.jpg) no-repeat center;
	background-size: cover;
    height: 100%;
	overflow: hidden;
	position: relative;
}

.swiper-wrapper {
    height: 100%;
}

.swiper-slide {
	position: relative;
    height: 100%;
}

.dark-layer {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0,0,0,0.51);
	pointer-events: none;
	z-index: 1;
}
.headings {
    color: #fff;
	text-align: center;
}
.banner__title {
    font-size: 40px;
    font-weight: 300;
    padding-bottom: 10px;
    margin: 0.5em 0;
}

.container-wrap {
	position: relative;
	z-index: 2;
}

.banner__text {
    font-family: 'RobotoCondensed';
    font-weight: 400;
    font-size: 19px;
    font-weight: 300;
    padding-bottom: 10px;
	margin: 0.6em 0;
}

.banner__quote {
    font-size: 18px;
    font-weight: 300;
    padding-bottom: 15px;
	margin: 0.6em 0;
    font-style: oblique;
    font-weight: 600;
}

.btn-os {
	display: block;
    color: #fff;
    border: 1px solid #fff;
    text-align: center;
    font-size: 26px;
    font-weight: 100;
    padding: 15px 0px;
    margin: 0px 0px 20px 0px;
    transition: 0.4s ease all;
    background: transparent;
    width: 150px;
}

.btn-os-andr {
	margin-left: auto;
}

.btn-os-ios {
	margin-right: auto;
}
.btn-os:hover{
	background: #fff;
	color: #000;
}



.full-height {
    height: 100%;
}

.features {
    height: 100%;
	background: #efefef;
	overflow: hidden;
	position: relative;
	font-family: 'RobotoCondensed', sans-serif;
}

.features__title {
    font-size: 40px;
	font-weight: 300;
    padding: 0 40px;
    margin: 40px 0 40px;
    line-height: 1.6;
    display: inline-block;
    position: relative;
    text-transform: uppercase;
}

.text-center {
	text-align: center;
}

.features__item{
    font-weight: 300;
}
.features__item-icn-wrap{
	text-align: center;
	padding: 10px 0;
}
.features__item-icn {
	max-height: 100px;
}

.features__item-title {
    font-weight: 300;
	margin: 0.6em 0;
}

.contacts {
    height: 100%;
    background: url(../img/map-image.png) no-repeat center;
    background-color: #222;
	color: #fff;
	font-family: 'RobotoCondensed', sans-serif;
}

.contacts__title {
	margin: 0 0 20px;
}

.contacts__input-wrap{
	margin-bottom: 20px;
}
.contacts__input-wrap:last-child{
	margin-bottom: 0;
}
.contacts__input {
	width: 100%;
	padding: 15px;
    border-radius: 0px;
    box-shadow: none;
    border: 2px solid #C2C2C2;
    font-size: 18px;
	color: #333;
}



.contacts__submit {
    width: 160px;
    background: #fff;
    color: #00ceb8;
    border: 2px solid #00ceb8;
    font-size: 46px;
    margin: 0 auto;
    margin-top: -46px;
	transition: 0.3s ease-in;
}
.contacts__submit:hover{
	transition: 0.3s ease-in;
	background: #00ceb8;
}

.contacts__submit-img {
	max-height: 55px;
    line-height: 55px;
    vertical-align: middle;
    fill: #00ceb8;
	transition: 0.3s ease-in;
}
.contacts__submit:hover .contacts__submit-img{
	transition: 0.3s ease-in;
	fill: #fff;
}
.display-flex{
  display: -ms-flexbox;
  display: flex;
}

.modal__wrap {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	display: flex;
	align-items: center;
	background: rgba(0,0,0,0.6);
	z-index: 10;
	visibility: hidden;
}
.modal__wrap.opened{
	visibility: visible;
}
.modal__wrap.opened .modal{
	visibility: visible;
	top: 0;
	animation: bounceIn .6s ease-in 1;
}

.modal {
	position: relative;
	visibility: hidden;
	top: -150%;
	background: #fff;
	width: 70%;
	margin: 0 auto;
	padding: 20px 5px;
	text-align: center;
	transition: 0.3s ease-in;
}

.modal__input {
	width: 50%;
	padding: 15px;
    border-radius: 0px;
    box-shadow: none;
    border: 2px solid #C2C2C2;
    font-size: 18px;
	color: #333;
}

.modal__title {
	margin: 0 0 20px;
}

.modal__submit {
    margin: 20px 0;
    width: 140px;
    height: 40px;
    border: 2px solid #C2C2C2;
    background: #fff;
    transition: 0.3s ease-in;
}
.modal__submit:hover {
    border: 2px solid #00ceb8;
    background: #00ceb8;
    color: #fff;
    transition: 0.3s ease-in;
}

.modal__close {
    position: absolute;
    top: 6px;
    right: 15px;
    cursor: pointer;
    font-family: 'Impact';
    font-style: normal;
    font-size: 36px;
    line-height: 24px;
}

@keyframes bounceIn{
	0%  { 
		visibility: visible;
		top: -150%;
	}
	50% { 
		top: 0%;
		transform: translateY(150px);
	}
	75% { 
		transform: translateY(-100px);
	}
	100%{
		transform: translateY(0);
	}
}